  font-family: "hiragino-mincho";
  src: url("/assets/font/hiragino-mincho-w6.woff") format("woff");
  font-weight: 600;
}
:root {
  --font-hiragino: "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", "Hiragino Sans", "メイリオ", "Meiryo", sans-serif;
  --font-yu-gothic: "游ゴシック体", YuGothic, "游ゴシック", "Yu Gothic", "メイリオ", "Hiragino Kaku Gothic ProN", "Hiragino Sans", sans-serif;
  --font-yu-mincho: "游明朝体", "Yu Mincho", YuMincho, "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", "MS P明朝", "MS PMincho", serif;
  --font-hiragino-mincho: hiragino-mincho, "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", "游明朝体", "Yu Mincho", YuMincho, "MS P明朝", "MS PMincho", serif;
  --font-noto-sans: "Noto Sans JP", "メイリオ", Meiryo, sans-serif;
  --font-noto-serif: "Noto Serif JP", sans-serif;
  --primary-color: #c9c7c0;
  --mv-padding-top: 180px;
  --mv-padding-right: 180px;
  --drawer-animation-speed: 0.8s;
  --drawer-animation-delay: 0.4s;
}

html {
  font-size: 0.6944444444vw;
}
@media (max-width: 750px) {
  html {
    font-size: calc(1000vw / 390);
  }
}
@media (min-width: 751px) {
  html {
    font-size: 0.6944444444vw;
  }
}
@media (max-width: px) {
  html {
    font-size: calc(1000vw / 1440);
  }
}

body {
  -webkit-text-size-adjust: 100%;
  overflow-x: hidden;
  font-family: var(--font-hiragino-mincho);
}

img {
  width: 100%;
