}

@media (max-width: 1440px) {
  :root {
    --mv-padding-top: max(90px, 18rem);
    --mv-padding-right: max(90px, 18rem);
  }
}
@media screen and (min-width: 0px) and (max-width: 750px) {
  :root {
    --mv-padding-top: 6.6rem;
    --mv-padding-right: 6.4rem;
    --mv-padding-bottom: 6.6rem;
  }
}
.top-page main .mv {
  position: relative;
  width: 100%;
  height: 100vh;
}
.top-page main .mv h1 {
  position: absolute;
  top: 5.4rem;
  left: 5.8rem;
  z-index: 1;
  width: 5.9rem;
  aspect-ratio: 59/392;
}
.top-page main .mv .splide-wrapper {
  width: 100%;
  height: 100%;
  padding: var(--mv-padding-top) var(--mv-padding-right) 0 0;
  transition: transform var(--drawer-animation-speed);
}
.top-page main .mv .splide-wrapper .splide {
  position: absolute;
  bottom: 0;
  overflow: hidden;
  width: calc(100% - var(--mv-padding-right));
  height: calc(100% - var(--mv-padding-top));
}
.top-page main .mv .splide-wrapper .splide .splide__track {
  width: 100%;
  height: 100%;
}
.top-page main .mv .splide-wrapper .splide .splide__track .splide__list .splide__slide figure {
  position: relative;
  width: 100%;
  height: 100%;
}
.top-page main .mv .splide-wrapper .splide .splide__track .splide__list .splide__slide figure::after {
  content: "";
  position: absolute;
  inset: 0;
  background-image: linear-gradient(128deg, rgba(255, 255, 255, 0) 40%, rgba(0, 0, 0, 0.8) 100%);
}
.top-page main .mv .splide-wrapper .splide .splide__track .splide__list .splide__slide figure .image-inner {
  width: 100%;
  height: 100%;
  scale: 1.1;
}
.top-page main .mv .splide-wrapper .splide .splide__track .splide__list .splide__slide figure .image-inner img {
  height: 100%;
  object-fit: cover;
}
.top-page main .mv .splide-wrapper .splide .splide__track .splide__list .splide__slide figure .caption-inner {
  position: absolute;
  top: 90%;
  right: 5rem;
  transform: translateY(-50%);
  z-index: 1;
  clip-path: polygon(0 0, 100% 0, 100% 50%, 0 50%);
}
.top-page main .mv .splide-wrapper .splide .splide__track .splide__list .splide__slide figure .caption-inner .slide-caption {
  color: #fff;
  text-align: end;
  font-size: 1.8rem;
  line-height: 1.5;
  letter-spacing: 0.04em;
}
.top-page main .mv .splide-wrapper .splide .splide__track .splide__list .splide__slide figure .caption-inner .slide-caption small {
  display: block;
  margin-top: 1rem;
  font-size: 1rem;
}
.top-page main .mv .splide-wrapper .splide .splide__track .splide__list .splide__slide.is-active figure .image-inner {
  animation: right-to-left 12s linear infinite;
  animation-play-state: running;
}
.top-page main .mv .splide-wrapper .splide .splide__track .splide__list .splide__slide input {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.top-page main .mv .splide-wrapper .splide .slide-counter {
  position: absolute;
  top: 68%;
  right: 5rem;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  color: #fff;
  font-size: 2.3rem;
  font-weight: 600;
  letter-spacing: 0.1em;
}
.top-page main .mv .splide-wrapper .splide .slide-counter .slide-divider {
  display: block;
  width: 100%;
  height: 0.2rem;
  rotate: -60deg;
  background-color: #fff;
}
.top-page main .mv .bg {
  position: absolute;
  inset: 0;
  z-index: -1;
  background-color: var(--primary-color);
  filter: blur(4px);
}
.top-page main .mv .bg img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.top-page main .mv .bg::after {
  content: "";
  position: absolute;
  inset: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: var(--primary-color);
  opacity: 0.8;
}

body.is-drawer-open .top-page main .splide-wrapper {
  transform: translateX(-33.6rem);
}

.rectangle {
  position: absolute;
  top: 0;
  right: 0;
  width: 141.4%;
  height: 2px;
  background-color: #fff;
  transform: rotate(-45deg);
