.site-header {
  position: absolute;
  top: 0;
  right: -21.2rem;
  z-index: 10;
  display: flex;
  gap: min(80px, 8rem);
  transition: right var(--drawer-animation-speed);
}
.site-header .button-inner {
  display: grid;
  place-items: center;
}
.site-header .button-inner .drawer-button {
  position: relative;
  z-index: 1;
  width: 2rem;
  height: 10rem;
}
.site-header .button-inner .drawer-button::before, .site-header .button-inner .drawer-button::after {
  content: "";
  position: absolute;
  display: block;
  width: 0.6rem;
  height: 3.2rem;
  background-color: #fff;
  transition: transform 0.6s;
}
.site-header .button-inner .drawer-button::before {
  bottom: 0;
  left: 0;
  transform: translateY(-75%);
}
.site-header .button-inner .drawer-button::after {
  top: 0;
  right: 0;
  transform: translateY(75%);
}
.site-header .button-inner .drawer-button:hover::before {
  transform: translateY(-50%);
}
.site-header .button-inner .drawer-button:hover::after {
  transform: translateY(50%);
}
.site-header .global-nav {
  display: grid;
  align-items: center;
  width: 21.2rem;
  min-height: 100vh;
}
.site-header .global-nav > ul {
  display: flex;
  flex-direction: column;
  gap: 10rem;
}
.site-header .global-nav > ul li a {
  display: block;
  padding: 0 0 1rem 0.4rem;
  border-bottom: 1px solid;
  width: 100%;
  color: #fff;
  font-size: 2.2rem;
  font-weight: 600;
  letter-spacing: 0.4rem;
  clip-path: polygon(0 0, 9.6% 0, 9.6% 100%, 0% 100%);
  transition: clip-path 0.6s;
}
.site-header .global-nav > ul li a:focus {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
}
@media (hover: hover) and (pointer: fine) {
  .site-header .global-nav > ul li a:where(:any-link, :enabled, summary):hover {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }
}
.site-header .global-nav > ul li:first-of-type a {
  padding: 0 0 1rem 0.1rem;
}

body.is-drawer-open .site-header {
  right: 0;
}
body.is-drawer-open .site-header .button-inner .drawer-button::before {
  transform: translateY(0);
}
body.is-drawer-open .site-header .button-inner .drawer-button::after {
  transform: translateY(0);
}
body.is-drawer-open .site-header .button-inner .drawer-button:hover::before {
  transform: translateY(-50%);
}
body.is-drawer-open .site-header .button-inner .drawer-button:hover::after {
  transform: translateY(50%);
}

