@use "../../base/shortcut-functions" as f;
@use "../modules" as modules;

.test-page {
  main {
    .mv {
      position: relative;
      width: 100%;
      height: 100vh;

      h1 {
        position: absolute;
        top: 5.4rem;
        left: 5.8rem;
        z-index: 1;
        width: 5.9rem;
        aspect-ratio: 59/392;
      }

      .splide-wrapper {
        .splide {
          position: absolute;
          bottom: 0;
          overflow: hidden;
          width: 125.6rem;

          .splide__list {
            .splide__slide {
              figure {
                position: relative;

                &::after {
                  content: "";
                  position: absolute;
                  inset: 0;
                  background-image: linear-gradient(128deg, rgba(255, 255, 255, 0) 40%, rgba(0, 0, 0, 0.8) 100%);
                }

                .caption-inner {
                  position: absolute;
                  top: 80%;
                  right: 5rem;
                  z-index: 1;
                  clip-path: polygon(0 0, 100% 0, 100% 50%, 0 50%);

                  .slide-caption {
                    color: #fff;
                    text-align: end;
                    font-size: 1.8rem;
                    line-height: 1.5;
                    letter-spacing: 0.04em;

                    small {
                      display: block;
                      margin-top: 1rem;
                      font-size: 1rem;
                    }
                  }
                }
              }
            }
          }

          .slide-counter {
            position: absolute;
            top: 58%;
            right: 5rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 3rem;
            color: #fff;
            font-size: 2.3rem;
            font-weight: 600;
            letter-spacing: 0.1em;

            .slide-divider {
              display: block;
              width: 100%;
              height: 0.2rem;
              rotate: -60deg;
              background-color: #fff;
            }
          }
        }
      }

      .bg {
        position: absolute;
        inset: 0;
        z-index: -1;
        background-color: var(--primary-color);
        filter: blur(4px);

        img {
          position: absolute;
          inset: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        &::after {
          content: "";
          position: absolute;
          inset: 0;
          display: block;
          width: 100%;
          height: 100%;
          background-color: var(--primary-color);
          opacity: 0.8;
        }
      }
    }
  }
}

